body {
  font-family: "Roboto";
  margin: 0;
}

.container {
  width: 1440px;
  padding-top: 96px;
}

.compotent {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  gap: 95px;
}
.compotent .content {
  width: 380px;
  color: #121212;
}
.compotent .content h2 {
  font-size: 42px;
  line-height: 50.4px;
  margin: 0 0 24px 0;
}
.compotent .content p {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
}
.compotent .content button {
  padding: 16px 40px;
  border-radius: 24px;
  font-size: 16px;
  line-height: 16px;
  background-color: #E4C1AE;
  color: white;
  font-weight: 700;
  border: none;
  margin-top: 32px;
  transition: background-color 200ms;
}
.compotent .content button:hover {
  background-color: #deb29b;
}
.compotent .content button:focus {
  background-color: #ead0c1;
}

.img-hero {
  width: 505px;
}

.img-hero img {
  width: 505px;
  height: 330px;
}

.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #121212;
  opacity: 0.5;
  z-index: 1;
}
.modal .content {
  background-color: white;
  z-index: 2;
  padding: 56px 95px;
  position: relative;
  width: 920px;
}
.modal .content .close {
  position: absolute;
  top: 48px;
  right: 48px;
  width: 24px;
  height: 24px;
  background: url("/images/x-icon.svg");
  border: none;
  transition: opacity 100ms;
}
.modal .content .close:hover {
  opacity: 0.3;
}
.modal .content h2 {
  margin: 0;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
}
.modal .content p {
  margin: 24px 0 0 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}

.hidden {
  display: none;
}